.animation-page {
  height: 100%;
}
/*页面第一次加载时的动画过程*/
/* .refade-appear-active {
    transform: translateX(100%);
    transition: transform 0.5s ease-in;
} */
.fade-appear,
.fade-enter {
  opacity: 0;
}
.fade-appear-active,
.fade-enter-active {
  opacity: 1;
  transition: opacity 1s ease-in;
}
.fade-appear-done,
.fade-enter-done {
  opacity: 1;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 1s ease-in;
}
.fade-exit-done {
  opacity: 0;
}
